<template>
  <div style="padding-top: 100px">
    <el-form :model="form" label-width="120px" ref="form" :rules="rules">
      <el-form-item label="接口路径" prop="url">
        <el-input v-model="form.url" style="width: 450px"></el-input>
      </el-form-item>
      <el-form-item label="并发数">
        <el-input v-model="form.num" min="1" style="width: 200px" type="number"></el-input>
      </el-form-item>
      <el-form-item label="请求方式">
        <el-select v-model="form.type" style="width: 200px" placeholder="请选择" @change="onSelect">
          <el-option v-for="item in form.options"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Content-Type">
        <div>
          <el-radio-group v-model="form.mediaType">
            <el-radio label="none" :disabled="none" border>none</el-radio>
            <el-radio label="application/json" :disabled="json" border>application/json</el-radio>
            <el-radio label="application/x-www-form-urlencoded" :disabled="xForm" border>
              application/x-www-form-urlencoded
            </el-radio>
            <el-radio label="multipart/form-data" disabled border>multipart/form-data</el-radio>
          </el-radio-group>
        </div>
      </el-form-item>
      <el-form-item label="请求数据" prop="jsonData">
        <el-input v-model="form.json" :disabled="jsonData" resize="none" style="width: 700px" autosize :rows="4"
                  type="textarea" placeholder="请输入json格式数据"/>
      </el-form-item>
      <el-form-item style="padding-left: 200px">
        <el-button size="large" plain type="primary" @click="onSubmit('form')">开始</el-button>
      </el-form-item>
      <el-form-item label="展示返回数据">
        <el-switch v-model="showData" inline-prompt class="mb-2" active-text="展示" inactive-text="不展示"
                   @change="onShow($event)"/>
      </el-form-item>
    </el-form>
  </div>

  <div>
    <el-descriptions
        class="margin-top"
        :column="4"
        border>
      <el-descriptions-item width="100px">
        <template #label>
          <div class="cell-item">
            总请求数
          </div>
        </template>
        {{ resultData.totalNum }}
      </el-descriptions-item>
      <el-descriptions-item width="100px">
        <template #label>
          <div class="cell-item">
            执行时间(S)
          </div>
        </template>
        {{ resultData.totalTime }}
      </el-descriptions-item>
      <el-descriptions-item width="100px">
        <template #label>
          <div class="cell-item">
            成功请求
          </div>
        </template>
        {{ resultData.totalSuccess }}
      </el-descriptions-item>
      <el-descriptions-item width="100px">
        <template #label>
          <div class="cell-item">
            失败请求
          </div>
        </template>
        {{ resultData.totalError }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template #label>
          <div class="cell-item">
            QPS
          </div>
        </template>
        {{ resultData.qps }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template #label>
          <div class="cell-item">
            最大响应时间(ms)
          </div>
        </template>
        {{ resultData.maxTime }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template #label>
          <div class="cell-item">
            最小响应时间(ms)
          </div>
        </template>
        {{ resultData.minTime }}
      </el-descriptions-item>
    </el-descriptions>
  </div>
  <div v-show="showData" style="padding-top: 50px">
    <el-table border :data="resultData.resultEntityList" :row-class-name="tableRowClassName" style="width: 40%">
      <el-table-column type="index" width="50"/>
      <el-table-column prop="json" label="返回数据"/>
      <el-table-column prop="time" label="时间(ms)" width="70px"/>
      <el-table-column prop="status" label="状态" width="80px">
        <template #default="scope">
          <span>{{ scope.row.status === true ? '成功' : '失败' }}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>


import {ElMessageBox} from "element-plus";

export default {
  data() {
    const checkData = (rule, value, callback) => {
      if (!value && this.form.type === "POST") {
        return callback(new Error('数据不能为空'));
      }else{
        return callback();
      }
    };


    return {
      showData: false,
      json: true,
      xForm: true,
      jsonData: true,
      none: false,
      form: {
        url: '',
        num: 1,
        mediaType: 'none',
        json: '',
        options: [{
          value: 'GET',
          label: 'GET'
        }, {
          value: 'POST',
          label: 'POST'
        }, {
          value: 'PUT',
          label: 'PUT'
        }, {
          value: 'DELETE',
          label: 'DELETE'
        }],
        type: 'GET'
      },
      resultData: {
        totalNum: 0,
        totalTime: 0,
        totalSuccess: 0,
        totalError: 0,
        maxTime: 0,
        minTime: 0,
        qps: 0,
        resultEntityList: [{
          time: 0,
          json: '',
          status: false,
        }]
      },
      rules: {
        url: [
          {required: true, message: '请输入接口地址', trigger: 'blur'}
        ],
        jsonData: [
          {required: true, validator: checkData, trigger: 'blur'}
        ],
      }
    }
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let da = this.form;
          const fullscreenLoading = this.$loading({text: "请耐心等待"})

          const request = this.axios.create({
            baseURL: "http://127.0.0.1:10086/interface/testing",
            timeout: 1 * 60 * 1000
          })

          request.post("", da).then((response) => {
            if (response.status == 200) {
              fullscreenLoading.close()
              this.resultData = response.data
            }
          }).catch(() => {
            fullscreenLoading.close()
            ElMessageBox.alert('接口请求超时', '出错了')
          })
        }else{
          return false;
        }
      });

    },
    onSelect(event) {
      if (event === "POST") {
        this.form.mediaType = "application/json"
        this.none = true
        this.json = false
        this.jsonData = false
        this.xForm = false
      } else {
        this.form.mediaType = "none"
        this.none = false
        this.json = true
        this.jsonData = true
        this.xForm = true
      }

    }
    ,
    onShow(o) {
      if (o) {
        this.showData = true
      } else {
        this.showData = false
      }
    },
    tableRowClassName(row) {
      if (row.row.status) {
        return 'success-row';
      } else {
        return 'warning-row';
      }
    }
  }
}
</script>

<style scoped>
div {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
<style>
.el-table .warning-row {
  background: #f89e8f;
}

.el-table .success-row {
  background: #CAF9B0;
}
</style>